.h2GetInTouch,
.letUsKnow {
  align-self: stretch;
  position: relative;
  line-height: 120%;
}
.letUsKnow {
  font-size: var(--font-size-lg);
  line-height: 150%;
}
.sectionTitle {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.contactForm,
.container {
  display: flex;
  box-sizing: border-box;
  align-items: flex-start;
}
.container {
  width: 531px;
  flex-direction: column;
  padding: 0 var(--padding-5xl);
  justify-content: flex-start;
  gap: var(--gap-45xl);
}
.contactForm {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 633px;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-45xl) 0;
  justify-content: center;
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-inter);
}
