.formBlockLabel {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-sm);
  line-height: 150%;
  font-family: var(--font-inter);
  color: var(--color-black);
  text-align: left;
}
.formTextInput {
  border: 0;
  background-color: transparent;
  align-self: stretch;
}
.inputWrapper {
  height: 73px;
  flex-direction: column;
  gap: var(--gap-5xs);
}
.formTextarea,
.inputWrapper,
.textareaWrapper {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.formTextarea {
  border: 1px solid var(--color-black);
  font-weight: 500;
  font-family: var(--font-inter);
  font-size: var(--font-size-sm);
  background-color: var(--color-white);
  box-sizing: border-box;
  height: 100px;
  flex-direction: row;
  padding: var(--padding-xs);
}
.textareaWrapper {
  flex-direction: column;
  gap: var(--gap-5xs);
}
.sendMessage {
  position: relative;
  font-size: var(--font-size-xs);
  line-height: 140%;
  font-weight: 500;
  font-family: var(--font-inter);
  color: var(--color-white);
  text-align: left;
}
.form,
.formButton {
  align-self: stretch;
  display: flex;
}
.formButton {
  cursor: pointer;
  border: 0;
  padding: var(--padding-xs) var(--padding-5xl);
  background-color: var(--color-black);
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.form {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
